@use 'common/colours';
@use 'common/sizes';

form#letters-form {
  background: url('images/countdown.png') no-repeat;
  background-size: contain;
  min-height: 500px;
  padding: 420px 0 0 30px;

  input#letters {
    background: rgba(255, 255, 255, 0.8);
    font-size: sizes.$font-size-medium;
    padding: 0.3em;
    margin-right: 1rem;
    text-transform: uppercase;
    width: 11em;
  }
}

.big-button {
  background: linear-gradient(160deg, colours.$button-top, colours.$button-bottom);
  border-radius: 4px;
  color: white;
  font-size: sizes.$font-size-large;
  padding: 10px 15px;

  &:hover {
    background: linear-gradient(160deg, colours.$button-bottom, colours.$button-top);
  }

  &:disabled {
    opacity: 0.7;
  }
}

@media (max-width: 490px) {
  form input#letters {
    font-size: sizes.$font-size-small;
    width: 9em;
  }

  .big-button {
    font-size: sizes.$font-size-small;
    padding: 5px 8px;
  }
}
