@use 'common/colours';
@use 'common/sizes';
* {
  box-sizing: border-box;
  outline: none;
}

body {
  background: colours.$background-blue;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica,
    Arial, sans-serif;
  font-size: sizes.$font-size-medium;
  line-height: 1.5;
  padding: 0;
  color: white;
}

.container {
  width: 1000px;
  max-width: 90vw;
  margin: 0 auto;
}

a {
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}
